.weather-input-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 20px; /* Add margin at the bottom */
}

.weather-input-container {
  display: flex;
  align-items: center;
  z-index: 1;
}

.error-message-container {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255, 0, 0, 0.5);
  border-radius: 10px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.error-message {
  color: white;
  font-size: 18px;
  font-weight: bold;
}

.weather-input {
  border: none;
  border-radius: 50px;
  padding: 0.5rem;
  font-size: 1.2rem;
  width: 16rem; /* Adjusted width to make space for the button */
  opacity: 0.8;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.weather-button {
  border-radius: 50px;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  margin-top: 0; /* Remove margin-top to align with the input field */
  margin-left: 0.5rem;
  opacity: 0.8;
  cursor: pointer;
}
